.TableScaner {
    width: 80%;
    margin: auto;
    margin-top: 1em;
    text-align: center;
    border-radius: .5em;
}

.TableScaner thead {
    background-color: steelblue;
    font-size: 1.2em;
}

.TableScaner tbody tr:nth-child(2n+1) {
    background-color: #aaa;
}

.TableScaner tbody tr:nth-child(2n) {
    background-color: lightgray;
}


